.App{
    /*background-image: url("social-icons-four.jpg");*/
    background-repeat: no-repeat;
    height: 700px;
    background-position: center;
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);

}
p {
    font-size: 18px;
}
header.App-header {
    text-align: center;
   
}
.container-box {
    width: 300px;
    margin: auto;
    padding: 20px;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
}
label.ds-label {
    font-size: 18px;
}
.ds-button {
    width: 100px;
    height: 25px;
    background-color: transparent;
    color: black;
    border-radius: 5px;
    border: 1px solid black;
    /* border: 1px solid; */
    /* border: none; */
    margin-left: 0px;
    cursor: pointer;
}
.ds-button:hover{
    background-color: #4158F8;
    color: white;
}
input {
    border: none;
    border-radius: 3px;
    height: 20px;
    margin-left: 3px;
}
span.ds-text {
    font-size: 18px;
}
select{
    width: 110px;
    height: 30px;
    font-size: 17px;
}
select#second-person-for-ds {
    margin-left: 10px;
}
li {
    font-size: 18px;
    margin-bottom: 7px;
}
button.addperson {
    margin-top: 0px;
}
button.setrelationtype.ds-button {
    margin-left: 10px;
}
select#second-person {
    margin-top: 10px;
}
select#first-person-for-ds {
    margin-bottom: 10px;
}
@media only screen and (min-width: 960px) {
    .container-box{
        width: 800px;
        padding: 40px;
    }
    button.addperson {
        margin-top: 0px;
        margin-left: 10px;
    }
    .for-mobile{
        display: none;
    }
    select#second-person {
        margin-top: 0px;
    }
    select#first-person-for-ds {
        margin-bottom: 0px;
    }
    button.addrelationtype.ds-button {
        margin-left: 10px;
    }
    button.findds.ds-button {
        margin-left: 10px;
    }
}